import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-companies',
  templateUrl: './companies.component.html',
  styleUrls: ['./companies.component.css']
})
export class CompaniesComponent implements OnInit {

  @Input() private searchKeyword: String;
  @Input() private searchResults;
  @Input() private searchCount;
  @Input() private noPersonMatch;

  @Input() private activeIndex: Number;

  @Input() private parentComp;

  constructor() { }

  ngOnInit() {
  }

  setActiveIndex(index) {
    this.activeIndex = index;
    this.parentComp.activeTabIndex = index;
  }
}



