import { Component, OnInit, OnDestroy, NgZone } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SearchService } from '../../services/search.service';
import { FlashMessagesService } from 'angular2-flash-messages';

declare var google: any;

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit, OnDestroy {

  searchKeyword: String;
  searchKeywordPlaces: String="";
  autocomplete:any;
  ngZone:any;

  private searchResultsPeople = [];
  private searchResultsCompanies = [];
  private searchResultsPlaces = [];

  searchCountPeople: Number;
  searchCountCompanies: Number;
  searchCountPlaces: Number;

  private noPersonMatchPeople = true;
  private noPersonMatchCompanies = true;
  private noPersonMatchPlaces = true;

  activeTabIndex = 0;

  constructor(
    private flashMessagesService: FlashMessagesService,
    private searchService: SearchService,
    private route: ActivatedRoute,
    private zone: NgZone
  ) {
      this.init = this.init.bind(this);
      this.onSearch = this.onSearch.bind(this);
      this.onSearchPlaces = this.onSearchPlaces.bind(this);
      this.ngZone = zone;


  }

  ngOnInit() {
    this.route.params.subscribe(params => {
       this.searchKeyword = params['keyword'];
    });

    this.onSearchCompanies('');

    this.init();

  }

  ngOnDestroy() {

  }

  setActiveTab(index) {
    this.activeTabIndex = index;
  }

  onSearch(val) {
    this.searchKeyword = val; console.log(this.searchKeyword);
    this.searchResultsPeople = [];
    this.searchCountPeople = 0;
    this.noPersonMatchPeople = true;
    this.searchService.search(this.searchKeyword).subscribe(data => {
      if(data.success) {
        this.noPersonMatchPeople = data.noPersonMatch;
        if(this.noPersonMatchPeople) {

        } else {
          this.searchCountPeople = data.people.length;
          for(let i=0; i<data.people.length; i++) {
            let people = data.people[i];
            this.searchResultsPeople.push({
              id: people._id,
              fullname: people.fullName,
              city: people.location.city+', '+people.location.state+', '+people.location.country
            });
          }
        }
      } else {
        this.flashMessagesService.show(data.message, { cssClass: 'alert-danger', timeOut: 3000});

      }
    });
  }

  onSearchCompanies(keyword) {
    this.searchResultsCompanies = [];
    this.searchCountCompanies = 0;
    this.noPersonMatchCompanies = true;
    this.searchService.searchCompanies(this.searchKeyword).subscribe(data => {
      if(data.success) {
        this.noPersonMatchCompanies = data.noPersonMatch;
        if(this.noPersonMatchCompanies) {

        } else {
          this.searchCountCompanies = data.companies.length;
          for(let i=0; i<data.companies.length; i++) {
            let company = data.companies[i];
            this.searchResultsCompanies.push({
              id: company._id,
              fullname: company.fullName,
              location: company.location
            });
          }
        }
      } else {
        this.flashMessagesService.show(data.message, { cssClass: 'alert-danger', timeOut: 3000});

      }
    });
  }

  onSearchPlaces(city, state, country) {
    this.searchKeywordPlaces = city+', '+state+', '+country;

    console.log(this.searchKeywordPlaces);

    if(city == undefined || state == undefined || country == undefined) {
      this.flashMessagesService.show("Please input place exactly.", { cssClass: 'alert-danger', timeOut: 3000});
      return;
    }
    this.searchResultsPlaces = [];
    this.searchCountPlaces = 0;
    this.noPersonMatchPlaces = true;
    this.searchService.searchPlaces({city: city, state: state, country: country}).subscribe(data => {console.log(data);
      if(data.success) {
        this.noPersonMatchPlaces = data.noPersonMatch;
        if(this.noPersonMatchPlaces) {

        } else {
          this.searchCountPlaces = data.people.length;
          for(let i=0; i<data.people.length; i++) {
            let people = data.people[i];
            this.searchResultsPlaces.push({
              id: people._id,
              fullname: people.fullName,
              city: people.location.city+', '+people.location.state+', '+people.location.country
            });
          }
        }
      } else {
        this.flashMessagesService.show(data.message, { cssClass: 'alert-danger', timeOut: 3000});

      }
    });
  }

  init() {
    var options = {
          types: ['(cities)']
         };
    var input = document.getElementById('locationTextField');
    this.autocomplete = new google.maps.places.Autocomplete(input, options);

    this.autocomplete.addListener('place_changed', () => {
        this.ngZone.run(() => {
          var place = this.autocomplete.getPlace();

          var addressComponents = place.address_components;

          if(addressComponents == undefined || addressComponents.length == undefined) {
            this.flashMessagesService.show("Please input place exactly.", { cssClass: 'alert-danger', timeOut: 3000});
            return;
          }

          var arrayLength = addressComponents.length;
          var city = '';
          var state = '';
          var country = '';
          for (var i = 0; i < arrayLength; i++) {
              var addressComponent = addressComponents[i];
              var types = addressComponent.types;
              var typesArrayLength = types.length;
              for (var j = 0; j < typesArrayLength; j++) {
                  var type = types[j];
                  if (type == 'locality') {
                      city = addressComponent.long_name;
                  }
                  if (type == 'administrative_area_level_1') {
                      state = addressComponent.long_name;
                  }
                  if (type == 'country') {
                      country = addressComponent.long_name;
                  }
              }
          }
          this.onSearchPlaces(city, state, country);
        });
    })
  }

}



