import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-places',
  templateUrl: './places.component.html',
  styleUrls: ['./places.component.css']
})
export class PlacesComponent implements OnInit {

  @Input() public searchKeyword: String;
  @Input() private searchResults;
  @Input() private searchCount;
  @Input() private noPersonMatch;

  @Input() private activeIndex: Number;

  @Input() private parentComp;

  constructor() { }

  ngOnInit() {
  }

  setActiveIndex(index) {
    this.activeIndex = index;
    this.parentComp.activeTabIndex = index;
  }

}



