import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MdTabsModule, MdInputModule } from '@angular/material';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';
import {FocusModule} from 'angular2-focus';

import { AppComponent } from './app.component';
import { WelcomeComponent } from './components/welcome/welcome.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { PeopleComponent } from './components/people/people.component';
import { CompaniesComponent } from './components/companies/companies.component';
import { PlacesComponent } from './components/places/places.component';
import { LoginComponent } from './components/login/login.component';
import { SearchComponent } from './components/search/search.component';
import { RegisterComponent } from './components/register/register.component';
import { HomeComponent } from './components/home/home.component';

import { ValidateService } from './services/validate.service';
import { AuthService } from './services/auth.service';
import { SearchService } from './services/search.service';
import { FlashMessagesModule } from 'angular2-flash-messages';

import { AuthGuard } from './guards/auth.guard';
import { AddpeopleComponent } from './components/addpeople/addpeople.component';
import { AddcompanyComponent } from './components/addcompany/addcompany.component';

const appRoutes = [

  { path: '', component: HomeComponent },
  { path: 'login', component: LoginComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'welcome', component: WelcomeComponent, canActivate: [ AuthGuard ] },
  { path: 'search', component: SearchComponent },
  { path: 'addpeople', component: AddpeopleComponent, canActivate: [ AuthGuard ] },
  { path: 'addcompany', component: AddcompanyComponent, canActivate: [ AuthGuard ] },
  { path: '**', component: HomeComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    WelcomeComponent,
    NavbarComponent,
    PeopleComponent,
    CompaniesComponent,
    PlacesComponent,
    LoginComponent,
    SearchComponent,
    RegisterComponent,
    HomeComponent,
    AddpeopleComponent,
    AddcompanyComponent
  ],
  exports: [
    MdTabsModule
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRoutes),
    BrowserAnimationsModule,
    MdTabsModule,
    MdInputModule,
    FormsModule,
    HttpModule,
    FlashMessagesModule,
    FocusModule.forRoot()
  ],
  providers: [ValidateService, AuthService, SearchService, AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }



