import { Component, OnInit, NgZone } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';

declare var google: any;

@Component({
  selector: 'app-addcompany',
  templateUrl: './addcompany.component.html',
  styleUrls: ['./addcompany.component.css']
})
export class AddcompanyComponent implements OnInit {

  autocomplete:any;
  ngZone:any;

  fullName: String;
  location: String;
  image: String;
  overview: String;

  constructor(
    private authService: AuthService,
    private router: Router,
    private flashMessages: FlashMessagesService,
    private zone: NgZone
  ) {
    this.ngZone = zone;
  }

  ngOnInit() {
    this.init();
  }

  init() {
    var options = {
          types: ['(cities)']
         };
    var input = document.getElementById('locationTextField');
    this.autocomplete = new google.maps.places.Autocomplete(input, options);

    this.autocomplete.addListener('place_changed', () => {
        this.ngZone.run(() => {
          var place = this.autocomplete.getPlace();

          var addressComponents = place.address_components;
          if(addressComponents == undefined) {
            this.flashMessages.show("Please input place exactly.", { cssClass: 'alert-danger', timeOut: 3000});
            return;
          }
          if(addressComponents.length == undefined) {
            this.flashMessages.show("Please input place exactly.", { cssClass: 'alert-danger', timeOut: 3000});
            return;
          }

          var arrayLength = addressComponents.length;
          var city = '';
          var state = '';
          var country = '';
          for (var i = 0; i < arrayLength; i++) {
              var addressComponent = addressComponents[i];
              var types = addressComponent.types;
              var typesArrayLength = types.length;
              for (var j = 0; j < typesArrayLength; j++) {
                  var type = types[j];
                  if (type == 'locality') {
                      city = addressComponent.long_name;
                  }
                  if (type == 'administrative_area_level_1') {
                      state = addressComponent.long_name;
                  }
                  if (type == 'country') {
                      country = addressComponent.long_name;
                  }
              }
          }
          this.location = city+', '+state+', '+country;
        });

    });

  }

  onAddcompanySubmit() {
    const company = {
      fullName: this.fullName,
      location: this.location,
      image: this.image,
      overview: this.overview
    }

    this.authService.addCompany(company).subscribe(data => {
      if(data.success) {
        this.flashMessages.show(data.msg, {
          cssClass: 'alert-success',
          timeOut: 5000
        });
        this.router.navigate(['search']);
      } else {
        this.flashMessages.show(data.msg, {
          cssClass: 'alert-danger',
          timeOut: 5000
        });
        this.router.navigate(['addpeople']);
      }
    });
  }

}



